import React, { memo } from 'react';
import { useTransition } from 'react-spring';
import styled from 'styled-components';
import { Button, LinkButton } from '../components/Button';
import { Container } from '../components/Container';
import { Col, Row } from '../components/Grid';
import { Nominee } from '../components/Nominee';
import { useNomineesContext } from '../components/NomineeContext';
import { Placeholder } from '../components/Placeholder';
import { dateFormatter } from '../utils/helpers';
import { useGetRequest } from '../utils/useRequest';

const Heading = styled.h3`
  margin-top: 0;
  margin-bottom: 0.4em;
  display: flex;
  justify-content: space-between;
  align-items: center;
  margin-bottom: 1rem;

  ${Button} {
    font-size: 1rem;
  }
`;

const RowSection = styled(Row)`
  margin-bottom: 2rem;
`;

const ColOverride = styled(Col)`
  margin-bottom: 1rem;
`;

const ContainerWrapper = styled(Container)`
  margin-top: 2rem;

  hr {
    margin: 1rem 0;
  }
`;

const NomineeItem = styled(Nominee)`
  margin-bottom: 1rem;
`;

const InfoHeading = styled.h3`
  font-weight: 400;
  color: ${(p) => p.theme.colors.primary};
  margin-bottom: 0.5rem;
  margin-top: 0;
  font-size: 1rem;

  + p {
    margin-top: 0;
  }
`;

type NomineeAndPlaceholder = Partial<Nominee> & { placeholder: boolean };

export const NominationPage = memo((props) => {
  const [nominees] = useNomineesContext();
  const request = useGetRequest<NominationAccount>('/nomination');

  const list = React.useMemo(() => {
    if (request.loading) {
      return Array.from({ length: 3 }).map((_, index) => ({
        name: `${index}`,
        placeholder: true,
      })) as NomineeAndPlaceholder[];
    }

    return [
      ...nominees,
      ...(request.data?.nominations || []),
    ] as NomineeAndPlaceholder[];
  }, [request.loading, request.data, nominees]);

  const listTransitions = useTransition(list, (item) => item.name, {
    from: { opacity: 0 },
    enter: { opacity: 1 },
    leave: { opacity: 0, position: request.loading ? 'static' : 'absolute' },
  });

  return (
    <>
      <ContainerWrapper>
        <h3>Nomination details</h3>
        <Row>
          <ColOverride sm={6} md={3}>
            <InfoHeading>CPF Account</InfoHeading>
            <p>
              {request.loading ? (
                <Placeholder as="span" height={20} />
              ) : (
                <strong>{request.data?.account}</strong>
              )}
            </p>
          </ColOverride>
          <ColOverride sm={6} md={3}>
            <InfoHeading>Nomination Date</InfoHeading>
            <p>
              {request.loading ? (
                <Placeholder as="span" height={20} />
              ) : (
                <strong>{dateFormatter(request.data?.nominationDate)}</strong>
              )}
            </p>
          </ColOverride>
        </Row>
        <hr />
        <RowSection>
          <Col md={8}>
            <Heading>
              <span>Nominees</span>
              <LinkButton
                to="/nomination/add"
                appearance="primary"
                outline={false}
              >
                Add
              </LinkButton>
            </Heading>
            {listTransitions.map(({ props: styles, key, item }) => {
              if (item.placeholder) {
                return <Placeholder height={145} key={key} style={styles} />;
              }

              return <NomineeItem key={item.name} data={item as any} />;
            })}
          </Col>
        </RowSection>
      </ContainerWrapper>
    </>
  );
});

export default NominationPage;
